.user-modal .user-modal-container {
  border:.5rem solid #fba93d;
  border-top-right-radius: 30px;
  border-bottom-left-radius: 30px;
  background: url("../img/back_sing_up.png") center no-repeat;
  background-size: cover;
  font:500 1.6rem "Ubuntu";
  color:#ffffff;
}
.switcher li a {
  font:500 1.6rem "Ubuntu";
  color:#ffffff;
}
#signup .form {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 15px;
  text-align: center;
}
#login .form,
#reset-password {
  padding:15px;
}
.form p {
  margin: 10px auto;
  text-align: center;
}
.form input:focus {
  border:none;
}
input[type="text"],
input[type="password"],
input[type="email"] {
  text-align: center;
  width: 55%;
}
input[type="file"] {
  width:45%;
}
form .checkBox {
  margin:15px;
  font-weight: 300;
}
input[placeholder] {
  font-style: italic;
}
form input {
  margin-top: 15px;
  padding-bottom: 5px;
  background:none;
  border:none;
  border-bottom: .3rem solid #fba93d;
  font:500 1.6rem "Ubuntu";
  color:#ffffff;
}
input[type="submit"] {
  font:500 1.6rem "Ubuntu";
  color:#010f42;
  background:linear-gradient(to right, #ffaa2a, #ff880d);
  border-top-right-radius: 15px;
  border-bottom-left-radius: 15px;
  padding:10px 20px;
}
input[type="submit" i]:active {
  border-style: none;
}
/*--------------------------------- modal ----------------------------------*/
.user-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(24, 33, 37, 0.9);
  z-index: 3;
  overflow-y: auto;
  cursor: pointer;
  visibility: hidden;
  opacity: 0;
  -webkit-transition: opacity 0.3s 0, visibility 0 0.3s;
  -moz-transition: opacity 0.3s 0, visibility 0 0.3s;
  transition: opacity 0.3s 0, visibility 0 0.3s;
}
.user-modal.is-visible {
  visibility: visible;
  opacity: 1;
  -webkit-transition: opacity 0.3s 0, visibility 0 0;
  -moz-transition: opacity 0.3s 0, visibility 0 0;
  transition: opacity 0.3s 0, visibility 0 0;
}
.user-modal.is-visible .user-modal-container {
  -webkit-transform: translateY(0);
  -moz-transform: translateY(0);
  -ms-transform: translateY(0);
  -o-transform: translateY(0);
  transform: translateY(0);
}

.user-modal-container {
  position: relative;
  width: 90%;
  max-width: 600px;
  background: #FFF;
  margin: 3em auto 4em;
  cursor: auto;
  border-radius: 0.25em;
  -webkit-transform: translateY(-30px);
  -moz-transform: translateY(-30px);
  -ms-transform: translateY(-30px);
  -o-transform: translateY(-30px);
  transform: translateY(-30px);
  -webkit-transition-property: -webkit-transform;
  -moz-transition-property: -moz-transform;
  transition-property: transform;
  -webkit-transition-duration: 0.3s;
  -moz-transition-duration: 0.3s;
  transition-duration: 0.3s;
}
.user-modal-container .switcher:after {
  content: "";
  display: table;
  clear: both;
}
.user-modal-container .switcher li {
  margin: 0 15px;
  float: left;
  text-align: center;
  list-style:none;
}
.user-modal-container .switcher li:first-child a {
  border-radius: .25em 0 0 0;
}
.user-modal-container .switcher li:last-child a {
  border-radius: 0 .25em 0 0;
}
.user-modal-container .switcher a {
  display: inline-block;
  line-height: 50px;
  text-decoration: none;
}
.user-modal-container .switcher a.selected {
  color: #fba93d;
}
.user-modal .form-bottom-message {
  text-align: center;
}
.form-bottom-message a {
  color:#ffffff;
  font:300 italic 1.6rem "Ubuntu";
}

@media only screen and (min-width: 600px) {
  .user-modal-container {
    margin: 4em auto;
  }
}
@keyframes borderslide-3px {
  0% {
    padding-bottom: 1px;
  }
  50% {
    padding-bottom: 3px;
  }
}

@keyframes borderslide-2px {
  0% {
    padding-bottom: 1px;
  }
  50% {
    padding-bottom: 2px;
  }
}

.close-form {
  /* form X button on top right */
  display: block;
  position: absolute;
  width: 20px;
  height: 21px;
  right: 15px;
  top: 15px;
  background: url("../img/close.png") no-repeat center center;
  text-indent: 100%;
  white-space: nowrap;
  overflow: hidden;
}
@media only screen and (min-width: 1170px) {
  .close-form {
    display: none;
  }
}

.accept-terms {
  color: #F64747;
  text-decoration: none;
  padding: 0 1px 1px 2px;
  border-bottom: 1px solid rgba(246, 71, 71, .0);
  -webkit-transition: all 0.30s ease-in-out;
    -moz-transition: all 0.30s ease-in-out;
    -ms-transition: all 0.30s ease-in-out;
    -o-transition: all 0.30s ease-in-out;
}

.accept-terms:hover {
  animation: borderslide-2px .5s;
  padding-bottom: 2px;
  border-bottom: 1px solid rgba(246, 71, 71, 1);
}

#login, #signup, #reset-password {
  display: none;
}

#login.is-selected, #signup.is-selected,
 #reset-password.is-selected {
  display: block;
}