/*--------------------- office -------------------*/
.mainContent .user_office {
  background: url('../img/back_user_office.jpg') center no-repeat;
  background-size: cover;
  padding:50px 0;
}
.flex5 .office {
  padding:15px;
  background-color: rgba(0,0,0,.43);
  box-shadow: 0px 0px 20px 20px;
}
.blockText p,
.blockText #linkTour {
  font:normal 1.6rem "Ubuntu";
  color:#ffffff;
}
.blockText h4 {
  font:500 1.6rem "Ubuntu";
  color:#ffffff;
}
.blockText .exit {
  width: 22px;
  height:19px;
  background: url("../img/noun_logout.png") center no-repeat;
}
.blockText .btn {
  background: none;
  border:none;
  margin-top:3px;
  font:normal 1.6rem "Ubuntu";
  color:#f1b027;
}
#office .form {
  font:500 1.6rem "Ubuntu";
  color:#ffffff;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  padding: 15px;
  text-align: center;
}
#office form input {
  font:300 1.6rem "Ubuntu";
  color:#fcb827;
}
#office input[type="submit"] {
  border:none;
  font:500 1.6rem "Ubuntu";
  color:#010f42;
}
/*---------------------- @media 568px --------------------------*/
@media screen and (min-width:568px) {
  #office form p {
    width: 50%;
  }
}
/*--------------------- //office -------------------*/