* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
html {
    font-size:62.5%;
}
.gridContainer {
  min-width: 320px;
  max-width: 1100px;
  margin: 0 auto;
  }
h1 {
  font:bold 3rem "Ubuntu"; //замена шрифта на меньший для моб. версии 6rem
  color:#ffffff;
  position:relative;
}
h1::before {
  content:"";
  position:absolute;
  width:12px;
  height: 114px;
  border-radius:5px;
  background-color:#ffad2c;
  left:-25px;
  top:15px;
}
h2 {
  font:bold 2rem "Ubuntu"; //замена шрифта на меньший для моб. версии 3rem
  color:#ffffff;
  padding: 15px 30px;
  display: inline-block;
}
h3 {
  font:500 1.5rem "Ubuntu"; //замена шрифта на меньший для моб. версии
  color:#ffc23e;
}
.pH1, .pH2, .pH3 {
  font:normal 1.6rem "Ubuntu"; //замена шрифта на меньший для моб. версии 1.6rem
  color:#ffffff;
}
.pH1 {
  font-weight: bold;
  color:#ffffff;
}
.pH3 {
  font-size:2rem;  //замена шрифта на меньший для моб. версии 2.8rem
}
.pH2 {
  margin-top:30px;
}
a {
    display: inline-block;
    text-decoration: none;
    cursor: pointer;
}
button {
  cursor: pointer;
}
.wrapper .colorOrage {
  color:#ffc23e;
}
.wrapper .linkRegistry {
  font:500 1.6rem "Ubuntu";
  color:#010f42;
  background:linear-gradient(to right, #ffab2a, #ff890e);
  border-top-right-radius: 15px;
  border-bottom-left-radius: 15px;
  padding:15px 20px;
  margin-top:15px;
}
/*----------- авто блик --------*/
.custom-btn {
    position: relative;
    overflow: hidden;
   -webkit-transition: ease 0.3s;
    transition: ease 0.3s;
}
.custom-btn:before {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background: #fa4923;
    opacity: 0;
    z-index: 1;
    -webkit-transition: ease 0.3s;
    transition: ease 0.3s;
}
.custom-btn:after {
    content: "";
    display: block;
    width: 30px;
    height: 300px;
    margin-left: 60px;
    background: #fff;
    position: absolute;
    left: -40px;
    top: -150px;
    z-index: 1;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    -webkit-transition: all 0.1s;
    transition: all 0.1s;
    -webkit-animation-name: slideme;
    animation-name: slideme;
    -webkit-animation-duration: 3s;
    animation-duration: 3s;
    -webkit-animation-delay: 0.05s;
    animation-delay: 0.05s;
    -webkit-animation-timing-function: ease-in-out;
    animation-timing-function: ease-in-out;
    -webkit-animation-iteration-count: infinite;
    animation-iteration-count: infinite;
}
@-webkit-keyframes slideme { 0% { left: -30px; margin-left: 0px; }
  30% { left: 110%; margin-left: 80px; }
  100% { left: 110%; margin-left: 80px; } }
/*----------- //авто блик --------*/
/*----------------------------- header ---------------------------*/
.wrapper .header {
  width:100%;
    background-color: #0d122d;
}
.header .logoHeader {
  width: 89px;
  height: 72px;
  margin: 15px;
  border-radius: 10px;
  background: url("../img/logo_header.png") center no-repeat;
}
.header .flex {
    display:flex;
    justify-content: space-between;
    align-items: center;
}
.flex .menuBlock {
  display: flex;
  align-items: center;
  margin: 15px;
}
.flex .rulesLink,
.flex .officeLink {
  font:300 1.8rem "Ubuntu";
  color:#ffffff;
  margin-right: 20px;
}
.menuBlock .privatOffice {
  background:linear-gradient(to right, #ff981a, #ff7800);
  font:500 1.8rem "Ubuntu";
  color:#010f42;
  border:none;
  display: inline-flex;
  border-radius: 50%;
  align-items: center;
  vertical-align: middle;
}
.privatOffice .textBut {
  display: none;
}
/*--------------------- banner --------------------*/
.wrapper .banner {
  padding:50px 0;
  position: relative;
  background: url("../img/back_banner.jpg") center no-repeat;
    background-size: cover;
    height:600px;
}
.wrapper .banner::after {
  content:"";
  background:url("../img/soldier.png") center no-repeat;
  width:594px;
  height:497px;
  position:absolute;
  z-index: 1;
  bottom: 0;
  right: 0;
}
.flex1 .blockH1 {
  position:absolute;
  top: calc(50vh - (50% / 2));
  padding-left: 35px;
  z-index: 2;
}
.wrapper .banner::before {
  content:"";
  position:absolute;
  background:url("../img/polkadot.png") center no-repeat;
  width:184px;
  height:112px;
  top:10%;
  left:20px;
  z-index: 1;
}
/*-------------------- tournaments -------------------*/
.mainContent .tournaments {
  background: url("../img/back_tournaments.jpg") center no-repeat;
  background-size: cover;
  padding:30px 0;
  border-top: .4rem solid #fba93d;
}
.infoGame .timeGame {
  font-size:2rem;
  margin-top:15px;
}
.tournaments .flex2 {
  display: flex;
  flex-wrap:wrap; //замена на column
}
h2, .article {
  border:.5rem solid #fba93d;
  border-top-right-radius: 30px;
  border-bottom-left-radius: 30px;
}
.flex2 .blockH2 {
  padding:15px 0;
}
.flex2, .flex4 {
  background:url("../img/pubg_mobile.png") no-repeat;
  background-position: 60px 65px;
}
.flex2 .article {
  position: relative;
  width:85%;
  margin:15px auto;
  padding:15px;
}

.flex2 .article {
  background-color: rgba(0, 0, 0, 0.43);
}
.flex2 .article:first-of-type {
  background-color:rgba(127, 87, 2, 0.52);
}
.infoGame p {
  font:500 1.8rem "Ubuntu";
  color:#ffffff;
}
.article .infoGame {
  margin-top:15px;
}
.flex2 .tournamentInfo {
  position: relative;
  background:url("../img/back_info2.png") center no-repeat;
  background-size:cover;
  font:300 2rem "Ubuntu";
  color:#ffc23e;
  text-align: center;
  padding: 0;
  display: none;
}
.tournamentInfo .textInfo {
    position: absolute;
    height: 65%;
    width: 100%;
    padding: 40px 0 0 0;
    bottom: 0;
    border-top: .4rem solid #fba93d;
}
.tournamentInfo .textInfo::before {
  content:url("../img/!1.png");
  width: 30px;
  height:30px;
  border-radius: 50%;
  position: absolute;
  top:-15px;
  left:calc(50% - 15px);
  background:url("../img/back_info2.png") center;
}
.article .blockPlayers {
  //position: absolute;
  margin-top: 15px;
  top:0px;
  right: 15px;
  font:500 .919rem "Ubuntu";  //замена шрифта на меньший для моб. версии 2.4rem
  color:#ffffff;
}
.article .singUp {
  //position:absolute;
  bottom: 15px;
  right:15px;
  font:500 1.5rem "Ubuntu";
  color:#280032;
  background:linear-gradient(to right, #ffab2b, #ff8e13);
  border-top-right-radius: 10px;
  border-bottom-left-radius: 10px;
  padding:5px 10px;
  margin-top: 15px;
}
.article a,
.article p,
.article span,
.article .timeGame {
  font-size: 2rem;
}
/*----------------------- //tournaments --------------------*/

/*------------------------- platform -----------------------*/
.mainContent .platform {
  position: relative;
  padding:50px 0;
  background: url("../img/back_registry.jpg") center no-repeat;
  background-size: cover;
}
.flex3 .blockLink {
  width: 100%;
  display: flex;
  justify-content: space-around;
  margin: 30px 0 0;
  align-items:center;
  position: relative;
}
.blockLink .societyLink {
  display: flex;
  width: 100px;
  justify-content: space-between;
  align-items: center;
}
/*------------------------------------------
------------------------------------------*/
.linkInstagram,
.linkTelegram {
  width:40px;
  height:40px;
}
.societyLink .linkInstagram {
  background:url("../img/instagram_link.png") center no-repeat;
}
.societyLink .linkTelegram {
  background:url("../img/telegram_link.png") center no-repeat;
}
.wrapper .platform::before {
  content:"";
  position:absolute;
  background:url("../img/polkadot.png") center no-repeat;
  width:184px;
  height:112px;
  right:5%;
  top:10%;
  display: none;
}
/*-------------------------- foooter --------------------------*/
.wrapper .footer {
  position: relative;
  width: 100%;
  height:320px;
  background-color:#000f2e;
}
.footer .logoFooter {
  position: absolute;
  left:calc(50% - 100px);
  top: 40px;
  width:200px;
  height:107px;
  border-radius: 10px;
  background:url("../img/logo_footer.png") center no-repeat, #000f2e;
  z-index: 2;
}
.footer .textFooter {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding-top: 50px;
  border-top: .4rem solid #fba93d;
  z-index: 1;
}
.textFooter .infoSite {
  font:normal 1.6rem "Ubuntu";
  color:#ffffff;
  //padding:50px 0;
  margin:50px auto;
  text-align: center;
}
/*-------------------------- //foooter --------------------------*/
/*---------------------- @media 568px --------------------------*/
@media screen and (min-width:568px) {
  .privatOffice .textBut {
    display: inline;
    margin:5px 5px 5px 15px;
  }
  .menuBlock .privatOffice {
    vertical-align: 0;
    border-radius: 0;
    border-top-right-radius: 15px;
    border-bottom-left-radius: 15px;
  }
  .privatOffice img {
    margin:5px 10px;
  }
  .flex3 .blockLink {
    justify-content: space-between;
  }
  .blockLink .linkRegistry {
    position: absolute;
  margin-top: 0;
  left: calc(50% - 77px);
  }
}

/*---------------------- @media 667px --------------------------*/
@media screen and (min-width:667px) {
  .privatOffice .textBut {
    display: inline;
    margin:5px 5px 5px 15px;
  }
  .menuBlock .privatOffice {
    vertical-align: 0;
    border-radius: 0;
    border-top-right-radius: 15px;
    border-bottom-left-radius: 15px;
  }
  .privatOffice img {
    margin:5px 10px;
  }
  .article .singUp,
  .article .blockPlayers {
    position: absolute;
    //margin-top: 0;
  }
  .wrapper .platform::before {
    content: "";
    display: block;
  }
}
/*---------------------- @media 768 --------------------------*/
@media screen and (min-width:768px) {
  .flex2 .article,
  .flex2 .blockH2 {
    width: 48%;
  }
  .tournaments .flex2 {
    flex-direction: row;
    flex-wrap: wrap;
  }
  .flex2 .tournamentInfo {
    display: block;
  }
  .flex1 .blockH1 {
    top: calc(50vh - (100% / 2));
    width: 55%;
  }
  .blockH1 .pH1 {
    margin:15px 0 15px 50px;
  }
  .banner .linkRegistry {
    margin-left: 50px;
  }
  .article .blockPlayers,
  .article .singUp {
    position: static;
    margin-top: 15px;
  }
  .flex2 .article:nth-child(odd) {
    margin-left: 0;
  }
  .flex2 .article:nth-child(even) {
    margin-right:0;
  }
}
/*---------------------- @media 1024 --------------------------*/
@media screen and (min-width:1024px) {
  .flex1 .blockH1 {
    width:25%;
  }
  .wrapper .platform::before {
    content: "";
    right: calc(50% - 39rem);
  }
  .wrapper .banner::after {
    content: "";
    right: calc(50% - 50rem);
  }
  .article .blockPlayers,
  .article .singUp {
    position:absolute;
  }
}
/*---------------------- @media 1280 --------------------------*/
@media screen and (min-width:1280px) {
  .wrapper .banner::before {
    content: "";
    left:calc(50% - 55rem);
  }
  .wrapper .banner::after {
    content: "";
    right: calc(50% - 52rem);
  }
  h1 {
  font:bold 6rem "Ubuntu"; //замена шрифта на меньший для моб. версии 6rem
}
h2 {
  font:bold 3rem "Ubuntu"; //замена шрифта на меньший для моб. версии 3rem
}
.pH3 {
  font-size:2.8rem;  //замена шрифта на меньший для моб. версии 2.8rem
  }
}
